<template>
  <div v-if="isAuthResolved && isLocationResolved" id="app">
    <div class="page-wrapper">
      <router-view :key="$route.path" />
    </div>
    <TheFooter />
    <Slide right width="300">

      <div class="column">
        <div class="navbar-brand">
          <router-link :to="'/'" class="navbar-item" href="https://frequentlyasked.me">
          <h1 class="title is-4">frequentlyasked.me</h1>
          </router-link>
      </div>
      <div class="navbar-start row">
        <router-link :to="'/'" class="navbar-item">
          Home
        </router-link>
      </div>
      <div class="row">
            <router-link to="about" class="navbar-item">
            About
            </router-link>
      </div>

      <br />
        <div class="navbar-item">
          <div v-if="user">
            Welcome {{user.name}}
          </div>
        </div>
        <div v-if="user" class="navbar-item has-dropdown is-hoverable">
          <a class="navbar-link">
              Account
          </a>
          <div class="navbar-dropdown">
            <router-link :to="{name: 'PageProfile'}" class="navbar-item ">
                Profile
            </router-link>
            <hr class="navbar-divider">
            <a @click.prevent="logout" class="navbar-item ">
                Logout
            </a>
          </div>
        </div>
        <div v-else class="navbar-item has-dropdown ">
          <div class="buttons">
            <router-link :to="{name: 'PageRegister'}" class="button is-danger is-outlined is-hoverable">
              <strong>Sign up</strong>
            </router-link>
            <router-link :to="{name: 'PageLogin'}" class="button is-primary is-outlined is-hoverable">
              Log in
            </router-link>
          </div>
        </div>
        </div>
    </Slide>
  </div>
</template>

<script>
import TheFooter from '@/components/shared/TheFooter'
import { Slide } from 'vue-burger-menu'  // import the CSS transitions you wish to use, in this case we are using `Slide`
import {mapGetters} from 'vuex'

export default {
  name: 'app',
  components: {
    TheFooter,
    Slide
  },
  computed: {
    isAuthResolved () {
      return this.$store.state.auth.isAuthResolved
    },
    isLocationResolved () {
      return this.$store.state.meta.isLocationResolved
    },
    ...mapGetters({
        'user': 'auth/authUser'
    })
  },
  methods: {
  logout () {
        this.$store.dispatch('auth/logout')
          .then(() => {
            this.$router.push('/')
          })
      }
  },
  created () {
    this.$store.dispatch('meta/fetchMetaData')
  },
}
</script>

